  //è¿æ¯reactå°æ§å¶æäº¤è¿ç»æµè§å¨åï¼äº§ççç¬¬ä¸æ¬¡æ´æ°
  //è®¡ç®ä¸ä¸ªæ°çå¼å§æ¶é´
  currentEventTime = now();
