const reservedProps = new Set([
  'children',
  'dangerouslySetInnerHTML',
  // TODO: This prevents the assignment of defaultValue to regular
  // elements (not just inputs). Now that ReactDOMInput assigns to the
  // defaultValue property -- do we need this?
  'defaultValue',
  'defaultChecked',
  'innerHTML',
  'suppressContentEditableWarning',
  'suppressHydrationWarning',
  'style',
])

const attributeNameMap = new Map([
  ['acceptCharset', 'accept-charset'],
  ['className', 'class'],
  ['htmlFor', 'for'],
  ['httpEquiv', 'http-equiv'],
])

const shouldIgnoreAttribute = (name: string) => {
  if (reservedProps.has(name)) return true

  if (
    name.length > 2 &&
    (name[0] === 'o' || name[0] === 'O') &&
    (name[1] === 'n' || name[1] === 'N')
  ) {
    return true
  }

  return false
}

/**
 * ä¸ºdomåç´ è®¾ç½®å±æ§ï¼æ¯å¦å°classNameï¼data-*è®¾ç½®ä¸ºdomå±æ§
 * @param node è¦è®¾ç½®å±æ§çdom
 * @param name å±æ§çåç§°
 * @param value å±æ§çå¼
 */
export const setValueForProperty = (
  node: Element,
  name: string,
  value: unknown
) => {
  if (shouldIgnoreAttribute(name)) return

  const attributeName = attributeNameMap.get(name) ?? name
  if (value === null) {
    node.removeAttribute(attributeName)
  } else {
    node.setAttribute(attributeName, value + '')
  }
}
