const diffProperties = (
  domElement: Element,
  tag: string,
  lastRawProps: Record<string, any>,
  nextRawProps: Record<string, any>
): null | Array<unknown> => {
  let updatePayload: null | any[] = []

  let lastProps: Record<string, any>
  let nextProps: Record<string, any>

  switch (tag) {
    case 'input':
      lastProps = ReactDOMInputGetHostProps(domElement, lastRawProps)
      nextProps = ReactDOMInputGetHostProps(domElement, nextRawProps)
      updatePayload = []
      break
    case 'option':
    case 'select':
    case 'textarea':
      throw new Error('Not Implement')
    default: {
      lastProps = lastRawProps
      nextProps = nextRawProps
    }
  }

  let propKey
  let styleName
  let styleUpdates: Record<string, any> | null = null

  for (propKey in lastProps) {
    //è¯¥å¾ªç¯åªå¤çè¢«å é¤çprop
    if (
      nextProps.hasOwnProperty(propKey) ||
      (!lastProps.hasOwnProperty(propKey) && lastProps[propKey] == null)
    ) {
      continue
    }

    const nextProp = nextProps[propKey]
    if (propKey === STYLE) {
      throw new Error('Not Implement')
    } else if (propKey === CHILDREN) {
    } else {
      ;(updatePayload = updatePayload || []).push(propKey, null)
    }
  }

  for (propKey in nextProps) {
    //è¯¥å¾ªç¯ä¼å¤çå¢å åè¢«ä¿®æ¹çå±æ§
    const nextProp = nextProps[propKey]
    const lastProp = lastProps !== null ? lastProps[propKey] : undefined

    if (
      !nextProps.hasOwnProperty(propKey) ||
      nextProp === lastProp ||
      (nextProp === null && lastProp === null)
    ) {
      continue
    }

    if (propKey === STYLE) {
      if (lastProp) {
        for (styleName in lastProp) {
          //å¤çå é¤çstyle
          if (
            lastProp.hasOwnProperty(styleName) &&
            (!nextProp || !nextProp.hasOwnProperty(styleName))
          ) {
            if (!styleUpdates) {
              styleUpdates = {}
            }
            styleUpdates[styleName] = ''
          }
        }

        //å¤çæ°å¢æèæ´æ°çstyle
        for (styleName in nextProp) {
          if (
            nextProp.hasOwnProperty(styleName) &&
            lastProp[styleName] !== nextProp[styleName]
          ) {
            if (!styleUpdates) {
              styleUpdates = {}
            }
            styleUpdates[styleName] = nextProp[styleName]
          }
        }
      } else {
        if (!styleUpdates) {
          if (!updatePayload) {
            updatePayload = []
          }
          updatePayload.push(propKey, styleUpdates)
        }
        styleUpdates = nextProp
      }
    } else if (registrationNameDependencies.hasOwnProperty(propKey)) {
      if (!updatePayload) updatePayload = []
    } else if (propKey === CHILDREN) {
      //è¿éæ¯ç´æ¥ææ¬èç¹è½æ­£å¸¸æ´æ°çå³é®ï¼å ä¸ºä»ä»¬æ²¡æå¯¹åºçfiberèç¹
      //æä»¥ä¸è½é æä¸Updateæ ç­¾è¿ç§å½¢å¼å»æ´æ°ä»èªèº«çææ¬ï¼ä»åªè½å¨
      //ç¶èç¹çupdateQueue(ä¹å°±æ¯è¿çupdatePayload)ä¸­å ä¸ childrenå±æ§
      //å¾ä¼è¯¥èç¹ä¼æ´å·updateQueueä¸­childrençæ°åå®¹éæ°è®¾ç½®ææ¬
      if (typeof nextProp === 'string' || typeof nextProp === 'number') {
        ;(updatePayload = updatePayload || []).push(propKey, '' + nextProp)
      }
    } else {
      ;(updatePayload = updatePayload || []).push(propKey, nextProp)
    }
  }

  if (styleUpdates) {
    ;(updatePayload = updatePayload || []).push(STYLE, styleUpdates)
  }

  return updatePayload
}


