import { Dispatcher } from '../react-reconciler/ReactInternalTypes'
import { ReactCurrentDispatcher } from './ReactCurrentDispatcher'

type BasicStateAction<S> = ((a: S) => S) | S
type Dispatch<A> = (a: A) => void

/**
 * åå¾æ­¤æ¶å è¯¥ä½¿ç¨çDispatcher,æ¯å¦é¦æ¬¡mountæ¶çdispatcherå°±ä¸º
 * å°±ä¸ºHooksDispatcherOnMount
 * ç»ä»¶æ´æ°æ¶å°±ä¸ºHooksDispatcherOnUpdateï¼
 * å·ä½é»è¾å¯ä»¥æ¥çreact-reconciler/ReactFiberHooksä¸çrenderWithHookså½æ°
 * @returns 
 */
const resolveDispatcher = (): Dispatcher => {
  const dispatcher = ReactCurrentDispatcher.current
  return dispatcher!
}

/**
 * æ´å·å½åçdispatcherè°ç¨å¯¹åºçuseState
 * @param initialState åå§ç¶æ
 * @returns 
 */
export const useState = <S>(
  initialState: (() => S) | S
): [S, Dispatch<BasicStateAction<S>>] => {
  const dispatcher = resolveDispatcher()

  return dispatcher.useState(initialState)
}

export const useEffect = (
  create: () => (() => void) | void,
  deps: unknown[] | void | null
): void => {
  const dispatcher = resolveDispatcher()
  return dispatcher.useEffect(create, deps)
}

export const useLayoutEffect = (
  create: () => (() => void) | void,
  deps: unknown[] | void | null
): void => {
  const dispatcher = resolveDispatcher()
  return dispatcher.useLayoutEffect(create, deps)
}
