/**
 * å¨è¯¥èç¹è¢«å é¤åï¼è°ç¨å®çå¯ä½ç¨æ¸é¤å½æ°
 * (ä¹å°±æ¯useEffect,useLayoutEffectç¬¬ä¸ä¸ªå½æ°åæ°çè¿åå¼)ï¼å¦ææçè¯
 * @param finishedRoot
 * @param current
 * @param nearestMountedAncestor
 * @returns
 */
const commitUnmount = (
  finishedRoot: FiberRoot,
  current: Fiber,
  nearestMountedAncestor: Fiber
): void => {
  switch (current.tag) {
    case FunctionComponent: {
      const updateQueue: FunctionComponentUpdateQueue | null =
        current.updateQueue as any

      if (updateQueue !== null) {
        const lastEffect = updateQueue.lastEffect

        if (lastEffect !== null) {
          const firstEffect = lastEffect.next
          let effect = firstEffect

          do {
            const { destroy, tag } = effect
            if (destroy !== undefined) {
              if ((tag & HookLayout) !== NoHookEffect) {
                destroy()
              }
            }
          } while (effect !== firstEffect)
        }
      }
      return
    }
    case HostComponent: {
      //todo safelyDetachRef
      return
    }
    case HostText: {
      break
    }
    default:
      throw new Error('Not Implement')
  }
}

/**
 * æä»¥rootä¸ºæ ¹èç¹çå­fiberæ unmount
 * @param finishedRoot
 * @param root
 * @param nearestMountedAncestor
 * @returns
 */
const commitNestedUnmounts = (
  finishedRoot: FiberRoot,
  root: Fiber,
  nearestMountedAncestor: Fiber
) => {
  let node: Fiber = root

  //ä¸é¢çä»£ç ç¸å½äºdfsçè¿­ä»£çæ¬
  while (true) {
    commitUnmount(finishedRoot, node, nearestMountedAncestor)

    //å¦æè¯¥èç¹æå­èç¹æå­èç¹åä¸ç´å¾ä¸èµ°
    if (node.child !== null) {
      node.child.return = node
      node = node.child
      continue
    }

    if (node === root) return

    //è¯¥å±å·²ç»å¨é¨å¤çå®ï¼æ¯æ¶åè¿åä¸ä¸å±äº
    while (node.sibling === null) {
      if (node.return === null || node.return === root) {
        return
      }

      node = node.return
    }

    node.sibling.return = node.return
    node = node.sibling
  }
}

/**
 * ä»¥currentä¸ºæ ¹çå­fiberæ å³å°è¢«å é¤ï¼å°ä»éé¢çåå«çhost component
 * ä»domæ ä¸­å é¤
 * @param finishedRoot HostRoot
 * @param current è¦å é¤çå­æ çæ ¹èç¹
 * @param nearestMountedAncestor
 * @returns
 */
const unmountHostComponents = (
  finishedRoot: FiberRoot,
  current: Fiber,
  nearestMountedAncestor: Fiber
): void => {
  let node: Fiber = current

  let currentParentIsValid = false

  let currentParent
  let currentParentIsContainer

  while (true) {
    if (!currentParentIsValid) {
      /**
       * è¿éçé»è¾åªä¼å¨ç¬¬ä¸è½®å¾ªç¯æ¶è¿æ¥ä¸æ¬¡
       * å¨è¿éæ¾å°è¯¥å¾å é¤çå­æ çæè¿çä¸å±domèç¹
       * èèä»¥ä¸ä¾å­
       * function ChildToDelete() {
       *    return <div>ChildToDelete</div>
       * }
       *
       * function Wrapper({children}) {
       *    return children
       * }
       *
       * function Foo() {
       *   const [isShow, setIsShow] = useState(true)
       *
       *   return <div id="container">
       *        <Wrapper>
       *          {isShow ? <ChildToDelete /> : null}
       *        </Wrapper>
       *        <button onClick={() => setIsShow(!isShow)}>toggle</button>
       *    </div>
       * }
       * å½ç¹å»toggleæé®æ¶ChildToDeleteå½æ°ç»ä»¶ä¼è¢«å é¤,æ­¤æ¶ä¼æ²¿çChildToDelete
       * å¯¹åºçfiberèç¹åä¸æ¥æ¾ä¸ä¸ªHostComponentç±»åçèç¹,ä¸é¾çåºè¦æ¾çå°±æ¯é£ä¸ª
       * idä¸ºcontainerçdivèç¹èä¸æ¯Wrapperï¼å ä¸ºWrapperåªå¨Reactä¸­å­å¨
       * å®éä¸ChildTODeleteå­æ éé¢çdomèç¹æ¯æè½½å¨$('#container')ä¸ç
       *
       */
      let parent = node.return

      findParent: while (true) {
        const parentStateNode = parent?.stateNode
        switch (parent?.tag) {
          case HostComponent:
            //åä¸æ¥æ¾çè¿ç¨ä¸­éå°ä¸ä¸ªHostComponetï¼å°±ç´æ¥è¿å
            currentParent = parentStateNode
            currentParentIsContainer = false
            break findParent
          case HostRoot:
            //å¦æå·²ç»è¾¾å°HostRootï¼è¡¨æäºè¦å é¤çå­æ ä¸­çdomæ¯ç´æ¥æå¨
            //containerä¸çï¼é£ä»ä¹æ¯containerå¢ï¼
            //containerå°±æ¯ ReactDOM.renderçç¬¬äºä¸ªåæ°
            //æ¯å¦ReactDOM.render(<div></div>, element)
            //å¨è¿éä»å°±æ¯elementè¿ä¸ªdomåç´ 
            currentParent = parentStateNode.containerInfo
            currentParentIsContainer = true
            break findParent
        }
        parent = parent!.return
      }

      currentParentIsValid = true
    }

    //å¦æè¦å é¤ççå­æ çæ ¹èç¹ç´æ¥æ¯ä¸ä¸ªHostComponent,ä»domæ ä¸­å é¤ä»å¯¹åº
    //çåç´ 
    if (node.tag === HostComponent || node.tag === HostText) {
      commitNestedUnmounts(finishedRoot, node, nearestMountedAncestor)

      if (currentParentIsContainer) {
        throw new Error('Not Implement')
      } else {
        removeChild(currentParent, node.stateNode)
      }
    } else {
      //è¯¥èç¹ä¸æ¯HostComponent
      //ç»§ç»­è®¿é®ä»çå­èç¹ï¼å ä¸ºå¯è½è¿ä¼æ¾å°æ´å¤çhost components
      commitUnmount(finishedRoot, node, nearestMountedAncestor)
      if (node.child !== null) {
        node.child.return = node
        node = node.child
        continue
      }
    }

    if (node === current) return

    while (node.sibling === null) {
      if (node.return === null || node.return === current) return

      node = node.return
    }
    node.sibling.return = node.return
    node = node.sibling
  }
}

const detachFiberMutation = (fiber: Fiber) => {
  //åªç­returnæéå°ç»ç¹ä»æ ä¸­æ­å¼
  const alternate = fiber.alternate
  if (alternate !== null) {
    alternate.return = null
  }
  fiber.return = null
}

const commitDeletion = (
  finishedRoot: FiberRoot,
  current: Fiber,
  nearestMountedAncestor: Fiber
): void => {
  unmountHostComponents(finishedRoot, current, nearestMountedAncestor)

  detachFiberMutation(current)
}

const isHostParent = (fiber: Fiber): boolean => {
  return fiber.tag === HostComponent || fiber.tag === HostRoot
}

const getHostParentFiber = (fiber: Fiber): Fiber => {
  let parent = fiber.return

  while (parent !== null) {
    if (isHostParent(parent)) {
      return parent
    }

    parent = parent.return
  }

  throw new Error('Expected to find a host parent')
}
/**
 * æ¾å°ä¸ä¸ªfiberèç¹å³è¾¹é¦ä¸ªä¸éè¦æå¥çdomèç¹
 * @param fiber ä»è¯¥èç¹å¼å§å¾å³è¾¹æ¾
 * @returns æ¾å°çdomèç¹
 */
const getHostSibling = (fiber: Fiber): Element | null => {
  let node: Fiber = fiber

  siblings: while (true) {
    while (node.sibling === null) {
      if (node.return === null || isHostParent(node.return)) return null
      node = node.return
    }

    node.sibling.return = node.return
    node = node.sibling

    while (node.tag !== HostComponent) {
      if (node.flags & Placement) {
        continue siblings
      }

      if (node.child === null) {
        continue siblings
      } else {
        node.child.return = node
        node = node.child
      }
    }

    if (!(node.flags & Placement)) {
      return node.stateNode
    }
  }
}

const insertOrAppendPlacementNode = (
  node: Fiber,
  before: Element | null,
  parent: Element
): void => {
  const { tag } = node

  const isHost = tag === HostComponent || tag === HostText

  if (isHost) {
    const stateNode = isHost ? node.stateNode : node.stateNode.instance
    if (before) {
      insertBefore(parent, stateNode, before)
    } else {
      appendChild(parent, stateNode)
    }
  } else {
    const child = node.child
    if (child !== null) {
      insertOrAppendPlacementNode(child, before, parent)

      let sibling = child.sibling

      while (sibling !== null) {
        insertOrAppendPlacementNode(sibling, before, parent)
        sibling = sibling.sibling
      }
    }
  }
}

const insertOrAppendPlacementNodeIntoContainer = (
  node: Fiber,
  before: Element | null,
  parent: Container
): void => {
  const { tag } = node
  const isHost = tag === HostComponent || tag === HostText

  if (isHost) {
    const stateNode = node.stateNode

    if (before) {
      insertInContainerBefore(parent, stateNode, before)
    } else {
      appendChildToContainer(parent, stateNode)
    }
  } else {
    const child = node.child

    if (child !== null) {
      insertOrAppendPlacementNodeIntoContainer(child, before, parent)
      let sibling = child.sibling
      while (sibling !== null) {
        insertOrAppendPlacementNodeIntoContainer(sibling, before, parent)
        sibling = sibling.sibling
      }
    }
  }
}

const commitPlacement = (finishedWork: Fiber): void => {
  const parentFiber = getHostParentFiber(finishedWork)

  let parent
  let isContainer

  const parentStateNode = parentFiber.stateNode

  switch (parentFiber.tag) {
    case HostComponent:
      parent = parentStateNode
      isContainer = false
      break

    case HostRoot:
      parent = parentStateNode.containerInfo
      isContainer = true
      break
    default: {
      throw new Error('Invalid host parent fiber')
    }
  }

  if (parentFiber.flags & ContentReset) {
    resetTextContent(parent)
    parentFiber.flags &= ~ContentReset
  }

  const before = getHostSibling(finishedWork)

  if (isContainer) {
    insertOrAppendPlacementNodeIntoContainer(finishedWork, before, parent)
  } else {
    insertOrAppendPlacementNode(finishedWork, before, parent)
  }
}

