import { FiberRoot } from './ReactInternalTypes'

export const TotalLanes = 31

export type Lanes = number
export type Lane = number
export type LaneMap<T> = Array<T>

export const NoLanes: Lane = /*                         */ 0b0000000000000000000000000000000
export const NoLane: Lane = /*                          */ 0b0000000000000000000000000000000

export const SyncLane: Lane = /*                        */ 0b0000000000000000000000000000001
export const InputContinuousLane: Lanes = /*            */ 0b0000000000000000000000000000100
export const DefaultLane: Lanes = /*                    */ 0b0000000000000000000000000010000

export const IdleLane: Lanes = /*                       */ 0b0100000000000000000000000000000

const NonIdleLanes = /*                                 */ 0b0001111111111111111111111111111

export const NoTimestamp = -1

const clz32 = Math.clz32

const pickArbitraryLaneIndex = (lanes: Lanes): number => {
  return 31 - clz32(lanes)
}

/**
 * æ ¹æ®ä»»å¡çä¼åçº§ä¸ºå¶è®¡ç®ä¸ä¸ªè¿ææ¶é´
 * @param lane ä¼åçº§
 * @param currentTime å½åçæ¶é´
 * @returns
 */
const computeExpirationTime = (lane: Lane, currentTime: number): number => {
  switch (lane) {
    case SyncLane:
      return currentTime + 250
    case DefaultLane:
      return currentTime + 5000
    default: {
      throw new Error('Not Implement')
    }
  }
}

/**
 * å°å·²ç»è¿æçä»»å¡æ è®°åºæ¥
 * @param root FiberRoot
 * @param currentTime å½åçæ¶é´
 */
export const markStarvedLanesAsExpired = (
  root: FiberRoot,
  currentTime: number
): void => {
  const pendingLanes = root.pendingLanes
  const expirationTimes = root.expirationTimes

  let lanes = pendingLanes

  while (lanes > 0) {
    const index = pickArbitraryLaneIndex(lanes)
    const lane = 1 << index

    const expirationTime = expirationTimes[index]

    if (expirationTime === NoTimestamp) {
      /**
       * è¿æ²¡æç¸å³çæ¶é´æ³ï¼å¸®ä»è®¡ç®ä¸ä¸ª
       */
      expirationTimes[index] = computeExpirationTime(lane, currentTime)
    } else if (expirationTime <= currentTime) {
      //å·²ç»è¿æå°å¶å å¥å°expiredLanesä¸­
      root.expiredLanes |= lane
    }

    //ä»lanesä¸­ç§»é¤è¯¥laneï¼ä¸ä¸è½®å¾ªç¯å°±è½å¼å§æ£æµä¸ä¸ä¸ªlaneäº
    lanes &= ~lane
  }
}

/**
 * è¿åç°å¨è¢«å ç¨çlanesä¸­æé«ä¼åçº§çlane
 * ä¹å°±æ¯è·å¾ä¸ä¸ªæ°ä¸­ä»¥æä½ä½1æå½¢æçæ°å­ï¼åçå¯ä»¥å»æ¥çè´æ°çè¡¨ç¤º
 * æ¯å¦è¾å¥ 0b111 å°±è¿å 0b001
 * 0b101 -> 0b001
 * 0b100 -> 0b100
 * 0b1000001000 -> 0b0000001000
 * 0b1111111110 -> 0b0000000010
 * @param lanes
 * @returns
 */
export const getHighestPriorityLane = (lanes: Lanes): Lane => {
  return lanes & -lanes
}

/**
 * è¿åç°æçlanesä¸­æé«ä¼åçº§çlane
 * @param lanes
 * @returns
 */
const getHighestPriorityLanes = (lanes: Lanes | Lane): Lanes => {
  switch (getHighestPriorityLane(lanes)) {
    case SyncLane:
      return SyncLane
    case DefaultLane:
      return DefaultLane
    default: {
      throw new Error('Not Implement')
    }
  }
}

/**
 * æ ¹æ®å½årootçpendingLanesåworkInProgressLanesè¿åè¿æ¬¡æ´æ°çlanes
 * @param root
 * @param wipLanes
 * @returns
 */
export const getNextLanes = (root: FiberRoot, wipLanes: Lanes): Lanes => {
  const pendingLanes = root.pendingLanes

  //æåéåºï¼å¦ææ²¡æå¾è¿è¡çå·¥ä½
  if (pendingLanes === NoLanes) return NoLanes

  let nextLanes = NoLanes

  const nonIdlePendingLanes = pendingLanes & NonIdleLanes

  //å­å¨lanesè¢«å ç¨ï¼æ¾åºåªä¸ªæé«ä¼åçº§ç
  if (nonIdlePendingLanes !== NoLanes) {
    //è¿åç°è¢«å ç¨çlanesä¸­æé«ä¼åçº§çlane
    nextLanes = getHighestPriorityLanes(nonIdlePendingLanes)
  } else {
    throw new Error('Not Implement')
  }

  if (nextLanes === NoLanes) {
    return NoLanes
  }

  /**
   * å¦æå·²ç»å¤äºrenderé¶æ®µï¼åæ¢lanesä¼å¯¼è´ä¸¢å¤±è¿åº¦
   * æä»¬åªåºè¯¥å¨æ°çlaneæ¥ææ´é«çä¼åçº§çæ¶åè¿æ ·å
   */
  if (wipLanes !== NoLanes && wipLanes !== nextLanes) {
    const nextLane = getHighestPriorityLane(nextLanes)
    const wipLane = getHighestPriorityLane(wipLanes)

    //å¦æè¯¥æ¬¡ä»»å¡çä¼åçº§ä½äºç°å­ä»»å¡çä¼åçº§åworkInProgressLanesä¸å
    if (nextLane >= wipLane) {
      return wipLanes
    }
  }

  return nextLanes
}

export const includesSomeLane = (a: Lanes | Lane, b: Lanes | Lane): boolean => {
  return (a & b) !== NoLanes
}

export const mergeLanes = (a: Lanes | Lane, b: Lanes | Lane): Lanes => {
  return a | b
}

/**
 * è¿åè¯¥laneæå¨bitä½å¨bitsetä¸­index
 * æ¯å¦
 * 0b001 å°±ä¼è¿å0
 * 0b010 å°±ä¼è¿å1
 * 0b100 å°±ä¼è¿å2
 *
 * @param lane
 * @returns
 */
const laneToIndex = (lane: Lane): number => {
  return pickArbitraryLaneIndex(lane)
}

/**
 * æè¯¥æ¬¡æ´æ°çlaneå¹¶å°rootçpendingLanesä¸­ï¼ä»¥åè®°å½è¯¥æ´æ°å¯¹åºlaneçåççæ¶é´ï¼
 * æ¹ä¾¿ä»¥åå¯ä»¥å¤æ­è¯¥æ´æ°æ¯å¦å·²ç»è¿æéè¦ç«å³æ§è¡è¯¥æ´æ°
 * @param root FiberRoot
 * @param updateLane è¯¥æ´æ°å¯¹åºçlane
 * @param eventTime è¯¥æ´æ°åççæ¶é´
 */
export const markRootUpdated = (
  root: FiberRoot,
  updateLane: Lane,
  eventTime: number
): void => {
  //rootä¸åå«çæ´æ°ä»ä»¬æå¯¹åºçlanes
  root.pendingLanes |= updateLane

  //ä¸ä¸ªä¸åä¸ä½çæ°ç»ï¼åå«å¯¹åºç31ä½lane
  const eventTimes = root.eventTimes

  const index = laneToIndex(updateLane)
  eventTimes[index] = eventTime
}

export const createLaneMap = <T>(initial: T): LaneMap<T> => {
  const laneMap = []
  for (let i = 0; i < TotalLanes; ++i) {
    laneMap.push(initial)
  }

  return laneMap
}

/**
 * subset bitsetæ¯å¦æ¯ set bitsetçå­é
 * @param set
 * @param subset
 * @returns
 */
export const isSubsetOfLanes = (set: Lanes, subset: Lanes | Lane) => {
  return (set & subset) === subset
}

export const includesNonIdleWork = (lanes: Lanes): boolean => {
  return (lanes & NonIdleLanes) !== NonIdleLanes
}

/**
 * æ¯å¦å¼å¯é»è®¤åæ­¥æ¨¡å¼
 */
const enableSyncDefaultUpdates = false

/**
 * æ¯å¦å¼å¯æ¶é´åç,Reactä¸­é»è®¤å¼å¯äºåæ­¥æ¨¡å¼(enableSyncDefaultUpdates)ï¼æä»¥ä¸ä¼
 * å¼å¯æ¶é´åçï¼æä»¬è¿ä¸ºäºå­¦ä¹ çç®çæä»å³é­
 * @param root
 * @param lanes
 * @returns
 */
export const shouldTimeSlice = (root: FiberRoot, lanes: Lanes): boolean => {
  if ((lanes & root.expiredLanes) !== NoLanes) {
    //è³å°æä¸ä¸ªlaneå·²ç»è¿æäºï¼ä¸ºäºé²æ­¢æ´å¤çlaneè¿æ
    //å è¯¥å°½å¿«å®ææ¸²æï¼èä¸ææ§å¶æäº¤ç»æµè§å¨
    return false
  }

  if (enableSyncDefaultUpdates) {
    const SyncDefaultLanes = InputContinuousLane | DefaultLane

    return (lanes & SyncDefaultLanes) === NoLanes
  } else {
    return true
  }
}

/**
 * è¿è¡æ¬è½®æ´æ°çæ¶å°¾å·¥ä½ï¼å°å®æå·¥ä½çlane timeéç½®ï¼å¹¶å°ä»ä»¬
 * ä»pendingLanesï¼expiredLaneså»é¤
 * @param root
 * @param remainingLanes å©ä½è¦è¿è¡å·¥ä½çlanes
 */
export const markRootFinished = (root: FiberRoot, remainingLanes: Lanes) => {
  const noLongerPendingLanes = root.pendingLanes & ~remainingLanes

  root.pendingLanes = remainingLanes
  root.expiredLanes &= remainingLanes

  const eventTimes = root.eventTimes
  const expirationTimes = root.expirationTimes

  let lanes = noLongerPendingLanes

  while (lanes > 0) {
    const index = pickArbitraryLaneIndex(lanes)
    const lane = 1 << index
    eventTimes[index] = NoTimestamp
    expirationTimes[index] = NoTimestamp
    lanes &= ~lane
  }
}

export const removeLanes = (set: Lanes, subset: Lanes | Lane): Lanes => {
  return set & ~subset
}
