/**
 * æ ¹æ®JSXå¯¹è±¡çtypeåpropsåå»ºä¸ä¸ªfiberèç¹
 * @param type å¯ä»¥ä¸ºstringæ¯å¦div,på¯ä»¥ä¸ºå½æ°ï¼æ¯å¦å½æ°ç»ä»¶
 * å¯ä»¥ä¸ºç±»æ¯å¦ç±»ç»ä»¶å¯ä»¥ä¸ºSymbolæ¯å¦React.Fragment
 * @param key
 * @param pendingProps
 * @param mode fiberæ çæ¨¡å¼æ¯å¦Concurrent,Legacy
 * @param lanes
 * @returns
 */
export const createFiberFromTypeAndProps = (
  type: any,
  key: null | string,
  pendingProps: any,
  mode: TypeOfMode,
  lanes: Lanes
) => {
  let fiberTag: WorkTag = IndeterminateComponent
  let resolvedType = type

  if (typeof type === 'function') {
  } else if (typeof type === 'string') {
    fiberTag = HostComponent
  } else {
    getTag: switch (type) {
      default: {
        if (typeof type === 'object' && type !== null) {
          switch (type.$$typeof) {
            case REACT_MEMO_TYPE: {
              fiberTag = MemoComponent
              break getTag
            }
            default: {
              throw new Error('Not Implement')
            }
          }
        } else {
          throw new Error('Not Implement')
        }
      }
    }
  }

  const fiber = createFiber(fiberTag, pendingProps, key, mode)
  fiber.type = resolvedType
  fiber.lanes = lanes
  fiber.elementType = type
  return fiber
}
/**
 * æ ¹æ®èæDOMåå»ºfiberèç¹
 * @param element 
 * @param mode 
 * @param lanes 
 * @returns 
 */
export const createFiberFromElement = (
  element: ReactElement,
  mode: TypeOfMode,
  lanes: Lanes
): Fiber => {
  const type = element.type
  const key = element.key as any
  const pendingProps = element.props

  const fiber = createFiberFromTypeAndProps(
    type,
    key,
    pendingProps,
    mode,
    lanes
  )

  return fiber
}

