export const createFiberFromText = (
  content: string,
  mode: TypeOfMode,
  lanes: Lanes
): Fiber => {
  const fiber = createFiber(HostText, content, null, mode)
  fiber.lanes = lanes
  return fiber
}

export const isSimpleFunctionComponent = (type: any): boolean => {
  return typeof type === 'function' && type.defaultProps === undefined
}
