import { Fiber } from './ReactInternalTypes'

export type SharedQueue = {
  /**
   * å­å¨çæ¬æ¬¡æ´æ°çupdateéåï¼æ¯å®éçupdateQueueã
   * sharedçæææ¯currentèç¹ä¸workInProgressèç¹å±äº«ä¸æ¡æ´æ°éåã
   */
  pending: Update | null
}

export type UpdateQueue<State> = {
  /**
   * åä¸æ¬¡æ´æ°è®¡ç®å¾åºçç¶æï¼å®æ¯ç¬¬ä¸ä¸ªè¢«è·³è¿çupdateä¹åçé£äºupdateè®¡ç®å¾åºçstateãä¼ä»¥å®ä¸ºåºç¡è®¡ç®æ¬æ¬¡çstate
   */
  baseState: State
  shared: SharedQueue
  /**
   * åä¸æ¬¡æ´æ°æ¶updateQueueä¸­ç¬¬ä¸ä¸ªè¢«è·³è¿çupdateå¯¹è±¡
   */
  firstBaseUpdate: Update | null
  /**
   *lastBaseUpdateç¸å½äºï¼åä¸æ¬¡æ´æ°ä¸­ï¼updateQueueä¸­ä»¥ç¬¬ä¸ä¸ªè¢«è·³è¿çupdateä¸ºèµ·ç¹ä¸ç´å°çæåä¸ä¸ªupdateçå½¢æçé¾è¡¨ï¼æªåæåä¸ä¸ªèè·å¾çupdate
   */
  lastBaseUpdate: Update | null
}

export const UpdateState = 0

export type Update = {
  /**
   * æ´æ°ææºå¸¦çç¶æã
   * ç±»ç»ä»¶ä¸­ï¼æä¸¤ç§å¯è½ï¼å¯¹è±¡ï¼{}ï¼ï¼åå½æ°ï¼(prevState, nextProps):newState => {}ï¼
   *æ ¹ç»ä»¶ä¸­ï¼æ¯React.createElementåå»ºçJSXå¯¹è±¡ï¼å³ReactDOM.renderçç¬¬ä¸ä¸ªåæ°
   */
  payload: any

  /**
   * updateä¹é´éè¿nextå½¢æä¸æ¡é¾è¡¨ï¼ååå»ºçupdateæå¨é¾è¡¨åç«¯ï¼
   * å¶ä¸­æåä¸ä¸ªupdateï¼æååå»ºçï¼çnextæåç¬¬ä¸ä¸ªupdateå½¢æä¸ä¸ªç¯
   * æä»¥updateQueue.shared.pendingä¸ºæååå»ºçupdate
   */
  next: null | Update

  tag: 0 | 1 | 2 | 3
}

/**
 *åå§åfiberèç¹çupdateQueueï¼æ´æ°éåæ¯ä¸ªç¯å½¢é¾è¡¨
 *
 * @param fiber è¦åå§åupdateQueueçfiber
 */
export const initializeUpdateQueue = <State>(fiber: Fiber): void => {
  const queue: UpdateQueue<State> = {
    baseState: fiber.memoizedState,
    shared: {
      pending: null,
    },
    lastBaseUpdate: null,
    firstBaseUpdate: null,
  }

  fiber.updateQueue = queue
}

/**
 *
 * @returns åå»ºä¸ä¸ªå¨æ°çupdate
 */
export const createUpdate = (): Update => {
  const update: Update = {
    payload: null,
    next: null,
    tag: UpdateState,
  }
  return update
}

/**
 * å°ä¸ä¸ªupdateæ·»å fiberèç¹ä¸
 *
 * @param fiber è¦æ·»å updateçfiberèç¹
 * @param update è¯¥updateä¼è¢«æ·»å å°fiberèç¹çupdateQueueä¸
 */
export const enqueueUpdate = (fiber: Fiber, update: Update): void => {
  const updateQueue = fiber.updateQueue
  if (!updateQueue) return

  const sharedQueue: SharedQueue = (updateQueue as any).shared

  const pending: Update | null = sharedQueue.pending

  if (pending === null) {
    //ç¬¬ä¸ä¸ªåå»ºçupdateï¼åå»ºä¸ä¸ªå¾ªç¯é¾è¡¨
    update.next = update
  } else {
    //sharedQueue.pending å§ç»ä¸ºæåä¸ä¸ªåå»ºçupdate
    //sharedQueue.pending.nextæåç¬¬ä¸ä¸ªåå»ºçupdate,éåupdateé½æ¯ä»æ­¤å¼å§
    //æé¡ºåº1,2,3æå¥updateåææçé¾è¡¨ä¸º
    //______________
    //|             â
    //3  <-  2  <-  1

    //update3çnextæåææ©åå»ºçupdate1
    update.next = pending.next

    //update2çnextæåå½ååå»ºçupdate3
    pending.next = update
  }
  // æ°¸è¿æåææ°çæ´æ°
  sharedQueue.pending = update
}

/**
 * ä»current fiberä¸åéä¸ä¸ªupdateQueue
 * @param current
 * @param workInProgress
 */
export const cloneUpdateQueue = <State>(
  current: Fiber,
  workInProgress: Fiber
): void => {
  const queue: UpdateQueue<State> = workInProgress.updateQueue as any

  const currentQueue: UpdateQueue<State> = current.updateQueue as any

  if (queue === currentQueue) {
    const clone: UpdateQueue<State> = {
      shared: currentQueue.shared,
      firstBaseUpdate: currentQueue.firstBaseUpdate,
      lastBaseUpdate: currentQueue.lastBaseUpdate,
      baseState: currentQueue.baseState,
    }

    workInProgress.updateQueue = clone
  }
}
// æ ¹æ®èç¶æåæ°ç¶æï¼éè¿updateQueueè®¡ç®æ°ç¶æ
export const processUpdateQueue = <State>(
  workInProgress: Fiber,
  props: any,
  instance: any
) => {
  const queue: UpdateQueue<State> = workInProgress.updateQueue as any

  
  let firstBaseUpdate = queue.firstBaseUpdate
  let lastBaseUpdate = queue.lastBaseUpdate
  
  //æ£æµshared.pendingæ¯å¦å­å¨è¿è¡ä¸­çupdateå°ä»ä»¬è½¬ç§»å°baseQueue
  let pendingQueue = queue.shared.pending
  if (pendingQueue !== null) {
    queue.shared.pending = null

    const lastPendingUpdate = pendingQueue
    const firstPendingUpdate = lastPendingUpdate.next
    //æ­å¼æåä¸ä¸ªupdateåç¬¬ä¸ä¸ªupdateä¹é´çè¿æ¥
    lastPendingUpdate.next = null

    //å°shared.pendingä¸çupdateæ¥å°baseUpdateé¾è¡¨ä¸
    if (lastBaseUpdate === null) {
      firstBaseUpdate = firstPendingUpdate
    } else {
      lastBaseUpdate.next = firstPendingUpdate
    }

    lastBaseUpdate = lastPendingUpdate

    //å¦æcurrentå­å¨åè¿è¡ç¸åçå·¥ä½
    const current = workInProgress.alternate
    if (current !== null) {
      const currentQueue: UpdateQueue<State> = current.updateQueue as any
      const currentLastBaseUpdate = currentQueue.lastBaseUpdate

      if (currentLastBaseUpdate !== lastBaseUpdate) {
        if (currentLastBaseUpdate === null) {
          currentQueue.firstBaseUpdate = firstPendingUpdate
        } else {
          currentLastBaseUpdate.next = firstPendingUpdate
        }
        currentQueue.lastBaseUpdate = lastPendingUpdate
      }
    }
  }

  if (firstBaseUpdate !== null) {
    let newState = queue.baseState

    let newBaseState = null
    let newFirstBaseUpdate = null
    let newLastBaseUpdate = null

    let update: Update | null = firstBaseUpdate

    do {
      //ææ¶åè®¾ï¼æææ´æ°é½æ¯ä¸æ ·çä¼åçº§ï¼æ¯æ¬¡é½ä»ææupdateè®¡ç®ç¶æ
      newState = getStateFromUpdate(
        workInProgress,
        queue,
        update!,
        newState,
        props,
        instance
      )
      update = update!.next

      //å½åæ´æ°ä»¥å¨é¨éåå®ï¼ä½æ¯æå¯è½payloadä¸ºå½æ°å¨è®¡ç®stateçè¿ç¨ä¸­åå¨
      //updateQueue.shared.pendingä¸æ·»å äºæ°çæ´æ°ï¼ç»§ç»­è¿­ä»£ç´å°æ²¡ææ°æ´æ°äº§çä¸ºæ­¢
      if (update === null) {
        pendingQueue = queue.shared.pending
        //æ²¡äº§çæ°çæ´æ°
        if (pendingQueue === null) break
        else {
          //äº§çäºæ°çæ´æ°
          const lastPendingUpdate = pendingQueue
          const firstPendingUpdate = lastPendingUpdate.next
          lastPendingUpdate.next = null

          update = firstPendingUpdate
          queue.lastBaseUpdate = lastPendingUpdate
          queue.shared.pending = null
        }
      }
    } while (true)

    //ææ¶æ²¡æä¼è¢«è·³è¿çupdateå§ç»ä¸æç«
    if (newLastBaseUpdate === null) {
      newBaseState = newState
    }

    queue.baseState = newBaseState as any

    queue.firstBaseUpdate = newFirstBaseUpdate
    queue.lastBaseUpdate = newLastBaseUpdate
    // å½åå·¥ä½çfiberè®¾ç½®æ°ç¶æ
    workInProgress.memoizedState = newState
  }
}

export const getStateFromUpdate = <State>(
  _workInProgress: Fiber,
  _queue: UpdateQueue<State>,
  update: Update,
  prevState: State,
  nextProps: any,
  instance: any
): any => {
  switch (update.tag) {
    case UpdateState: {
      const payload = update.payload
      let partialState
      if (typeof payload === 'function') {
        partialState = payload.call(instance, prevState, nextProps)
      } else {
        partialState = payload
      }

      if (partialState === null || partialState === undefined) {
        //å¦ææ¯nullæèundefinedå°±è¯´æä»ä¹é½ä¸ç¨æ´æ°ï¼ä»ä¹ä¹ä¸å¹²
        return prevState
      }

      return Object.assign({}, prevState, partialState)
    }
  }
}
