const unstable_cancelCallback = (task: Task) => {
  /**
   * è¦å é¤ä¸ä¸ªä¼åéåä¸­çéæºåç´ ï¼éè¦O(N)çæ¶é´å¤æåº¦å¾ä¸åç®
   * ä¸å¦ç´æ¥æä»çcallbackç´æ¥èµå¼ä¸ºnullç¶åå¨popåºæ¥çæ¶åå¨å¤æ­ä¸ä¸
   * æ¯å¦å è¯¥å¿½ç¥ä»å°±è¡
   */
  task.callback = null
}

